<template>
    <div>
        <div class="row">
            <div class="col-12">
                <my-card title="PROTEÇÃO DA CRIANÇA E DO ADOLESCENTE NA ERA DIGITAL">
                    <div class="description-container">
                        <div class="description">
							A Equipe do Projeto de Extensão "Prevenção ao Aliciamento de Crianças e Adolescentes", coordenado pela Universidade Federal do Paraná em parceria com as Instituições identificadas ao lado, tem a honra de convidar Vossa Senhoria para o Encontro abaixo:
						</div>
                    </div>
                    <div class="meta">
                        <div class="meta-title">LOCAL DO EVENTO</div>
                        <div class="meta-description">
							Universidade Federal do Paraná, Campus Rebouças, Setor de Educação, Piso 1, Sala 112.<br>
							Rua Rockfeller, 57. Curitiba – Paraná.
						</div>
                    </div>
                    <div class="meta">
                        <div class="meta-title">DATA E HORÁRIO</div>
                        <div class="meta-description">
							29 de outubro de 2018 (terça-feira) das 14 horas até 18 horas.
						</div>
                    </div>
					<div class="meta">
                        <div class="meta-title">PROGRAMAÇÃO</div>
                        <div class="meta-description">
							14 horas: Abertura: Apresentação e Lançamento do Projeto. <br>
							14h15min: Mesa redonda. <br>
							15 horas: Intervalo.<br>
							15h15min: Reunião dos Grupos de Trabalho, conforme eixos temáticos.<br>
							17h15min: Apresentação dos Resultados de cada Grupo.<br>
							18 horas: Encerramento.
						</div>
                    </div>
                    <my-button url="inscricao">REALIZAR INSCRIÇÃO</my-button>
                </my-card>
            </div>
        </div>
    </div>
</template>

<script>
    import Button from "./components/Button.vue";
    import Card from "./components/Card.vue";
    import db from "./firebaseInit";
    export default {
        components: {
            'my-button': Button,
            'my-card': Card,
        },
    }
</script>

<style scoped lang="scss">
    .meta {
        margin: 5px 50px;
        color: #FFF;
        .meta-description {
            font-size: .9rem;
			line-height: 1.4;
			letter-spacing: .1em;
        }
        .meta-title {
            font-weight: bold;
            letter-spacing: 2px;
            margin-top: 20px;
            font-size: .95rem;
        }
    }
    span.info {
        display: block;
        text-align: center;
        color: #FFF;
        margin: 5px 0 35px 0px;
        font-size: 0.9rem;
        font-style: italic;
    }
    .description {
        margin: 30px 50px 0px 50px;
        line-height: 1.7;
        text-align: justify;
        color: #FFF;
        &::after {
            content: "";
            position: absolute;
            width: 80%;
            height: 0px;
            bottom: -2px;
            left: 10%;
            border-bottom: 2px solid #3AAFA9;
        }
    }
    .description-container {
        position: relative;
    }
    @media only screen and (max-width: 720px){
        .meta {
            margin: 5px 10px;
        }
        .description {
            margin: 20px 10px 25px 10px;
            line-height: 1.5;
            text-align: center;
        }
    }
</style>


