<template>
    <div id="app">
        <background-image></background-image>
        <div class="header">
            <a href="/">
                <img src="./assets/logo.png" class="logo borderless">
                <span class="brand">PROTECA</span>
            </a>
        </div>
        <div class="header right">
            <a target="_blank" href="http://www.ufpr.br/portalufpr/" title="Universidade Federal do Paraná">
                <img src="./assets/ufpr.jpeg" class="logo">
            </a>
			<a target="_blank" href="http://www.amigosdohc.org.br/" title="Associação dos Amigos do Hospital de Clínicas">
                <img src="./assets/aahc.jpg" class="logo">
            </a>
			<a target="_blank" href="https://www.facebook.com/institutotecnologiaedignidade/" title="Instituto Tecnologia e Dignidade Humana">
                <img src="./assets/itdh.jpg" class="logo">
            </a>
			<a target="_blank" href="https://www.unibrasil.com.br/" title="UNIBRASIL Centro Universitário">
                <img src="./assets/unibrasil.jpg" class="logo">
            </a>
        </div>
        <div class="container">
            <transition>
                <router-view/>
            </transition>
        </div>
    </div>
</template>

<script>
    import BackgroundImage from "./components/components/BackgroundImage.vue";
    export default {
        components: {
            'background-image': BackgroundImage,
        },
    }
</script>

<style scoped lang="scss">
    /** LOGO */
    .header {
		width: 125px;
        padding: 20px;
        position: absolute;
        &.right {
            right: 0;
        }
        .logo {
			width: 100%;
			&:not(.borderless){
				padding: 2px;
				background: white;
				border: 2px solid black;
				box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
				0 3px 1px -2px rgba(0, 0, 0, 0.12),
				0 1px 5px 0 rgba(0, 0, 0, 0.2);
			}
        }
        .brand {
            color: #17252A;
            font-weight: bolder;
            display: block;
            width: 100%;
            text-align: center;
			letter-spacing: 2px;
        }
        a {
			text-decoration: none;
        }
    }
    /** MEDIA QUERIES */
    @media only screen and (max-width: 1024px){
        .header {
            width: 100%;
            position: relative;
            padding: 0;
            padding-top: 30px;
            img{
                display: none;
            }
            .brand {
                display: block;
                width: 100%;
                text-align: center;
                letter-spacing: 2px;
                font-size: 2rem;
            }
        }
		.container {
            width: 100vw;
        }
    }
    /** ROUTER TRANSITIONS */
    .v-enter-active {
        animation: enterActive 300ms 300ms;
    }
    .v-leave-active {
        animation: leaveActive 300ms;
    }
    @keyframes enterActive {
        0% {
            opacity: 0;
            transform: translateY(-80vh);
        }
        100% {
            opacity: 1;
            transform: none;
        }
    }
    @keyframes leaveActive {
        0% {
            opacity: 1;
            transform: none;
        }
        100% {
            opacity: 0;
            transform: translateY(80vh);
        }
    }
</style>



