<template>
    <div class="row">
        <div class="col-12">
			<table>
				<thead>
					<th>Nome</th>
					<th>CPF</th>
					<th>E-MAIL</th>
					<th>OCUPAÇÃO</th>
				</thead>
				<tbody>
					<tr v-for="register in registrations" :key="register.id">
						<td>{{ register.name }}</td>
						<td>{{ register.cpf }}</td>
						<td>{{ register.email }}</td>
						<td>{{ register.ocupation }}</td>
					</tr>
				</tbody>
			</table>
        </div>
    </div>
</template>

<script>
    import db from "./firebaseInit.js";

    export default {
        data() {
            return {
				registrations: []
			}
        },
        created() {
            db.collection('registers').get().then(results => {
                results.forEach(result => {
					const record = {
						'cpf': result.data().cpf,
						'date': result.data().date,
						'email': result.data().email,
						'name': result.data().name,
						'ocupation': result.data().ocupation,
					}
					this.registrations.push(record);
				});
			})
        },
    }
</script>

<style lang="scss" scoped>
table {
	border: 1px solid #EBEBEB;
	margin-top: 50px;
    th {
        color: white !important;
		background: #17252A;
		line-height: 2;
        font-size:1.22rem;
        font-weight: 100;
    }
    tr {
        color:#17252A;
        &:hover {
            td {
                background:#3AAFA9;
                color:#FFFFFF;
                border-top: 1px solid #22262e;
                transition: 50ms;
                a {
                    background:#3AAFA9;
                    color:#FFFFFF;
                    transition: 50ms;
                }
            }
        }
        &:nth-child(odd) {
            td {
                background:#EBEBEB;
            }
            &:hover td {
                background:#3AAFA9;
            }
        }
        &:last-child {
            td {
                &:first-child {
                    border-bottom-left-radius:3px;
                }
                &:last-child {
                    border-bottom-right-radius:3px;
                }
            }
        }
    }
    td {
        background:#FFFFFF;
        text-align:left;
        vertical-align:middle;
        font-weight:300;
        font-size:.95rem;
		padding: 8px;
        &:last-child {
            border-right: 0px;
        }
    }
}
</style>



